@import '../constants';

.experience {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 20px 50px 0;
    border-top: solid 2px darkgoldenrod;
    padding-top: 20px;

  @include tabletUp {
    margin: 20px 50px 0;
  }
}

.experience-title {
    height: 100%;
    width: 100%;
    margin: 10px;
    font-weight: 500;
}

.projects-container {
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    padding: 0 20px;

    .project-container {
        margin: 10px;
    }
}

.project-popup-enter {
    opacity: 0;
}

.project-popup-enter-active {
    opacity: 1;
    transition: opacity 400ms;
}

.project-popup-exit {
    opacity: 1;
}

.project-popup-exit-active {
    opacity: 0;
    transition: opacity 400ms;
}
