@import '../constants';

.project {
  flex: 1 0 25%;
  width: 200px;
  border: solid 1px cornflowerblue;
  border-radius: 3px;

  &:hover {
    cursor: pointer;
    background-color: cornflowerblue;
    color: cornsilk;
    transition: 0.5s;
  }
}

.project-details {
  margin: 10px;
  font-size: 16px;
}

.core-information {
  p {
    margin: 0;
    font-size: 14px;

    &.core-title {
      font-size: 16px;
      font-weight: 500;
    }
  }
}

.tech-stack-list {
  margin-left: 30px;
}

.project-popup {
  position: fixed;
  z-index: 100;
  max-height: calc(100vh - 170px);
  overflow-y: auto;

  margin: 10px;
  padding: 5px;
  border-radius: 3px;
  top: 5vh;
  left: calc(10% - 15px);
  width: 80%;

  @include tabletUp {
    margin: 30px;
    padding: 15px;
    border-radius: 3px;
    top: 10vh;
    left: calc(17.5% - 45px);
    width: 65%;
  }
}

.description {
  & > p, ul {
    margin: 0;
  }

  & > p + p {
    margin-top: 1rem;
  }
}

.project-popup-close {
  float: right;
  cursor: pointer;
}

.project-date {
  display: grid;
  grid-template-columns: 90% 10%;
}

.project-popup-open {
  float: right;
  margin-top: 3px
}