@import './styles/global';

.app-common {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    font-size: 24px;
    color: black;
    padding: 0;

  @include tabletUp {
    padding: 0 20px;
  }

  @include desktopUp {
    padding: 0 80px;
  }

  @include xlDesktopUp {
    padding: 0 150px;
  }
}

.app-body {
  @extend .app-common;
  background-color: floralwhite;
}

.app-body-ff {
  @extend .app-common;
  background-color: black;
  color: white;
}